.skeleton-style {
    cursor: default !important;
}

.skeleton-style > .skeleton-ui-material {
    all: inherit;
    margin: 0;
    padding: 0;
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.11);
}